import React, { useState, useMemo, useEffect, useContext, useCallback } from 'react';
import FilterList from './FilterList';
import ProductList from './ProductList';
import './ListaProductosView.css'
import ProductosContext from '../../context/productosContext'
import Redes from '../shared/Redes'
import { useParams, useLocation } from 'react-router-dom';

function ListaProductosView({ showNavBar, selectedFilter }) {

  const search = useParams().search
  const location = useLocation()
  const productosContext = useContext(ProductosContext)

  //INNER FUNCTIONS
  const generateFilters = (products) => {
    if (products.length > 0) {
      const filters = []
      const productsFiltersKeys = Object.keys(products[0].filters)
      productsFiltersKeys.forEach(key => {
        filters.push({ title: key.toUpperCase(), buttons: [] })
      })
      products.forEach(product => {
        filters.forEach(filter => {
          if (!filter.buttons.includes(product.filters[filter.title.toLowerCase()]) && product.filters[filter.title.toLowerCase()] !== '') filter.buttons.push(product.filters[filter.title.toLowerCase()])
        })
      })
      filters.forEach(filter => filter.buttons.sort())
      return filters
    }
    return []
  }

  //STATE
  const productosOriginales = useMemo(() => {
    if (search) {
      const searchWords = search.split(' ')
      const productosSearch = []
      productosContext.forEach(producto => {
        const score = searchWords.reduce((accumulator, currentValue) => {
          if (JSON.stringify(producto.title).toLowerCase().includes(currentValue.toLowerCase())) accumulator = accumulator + 5
          if (JSON.stringify(producto.description).toLowerCase().includes(currentValue.toLowerCase())) accumulator = accumulator + 2
          return accumulator
        }, 0)
        if (score > 0) productosSearch.push([producto, score])
      })
      productosSearch.sort((a, b) => a[1] > b[1] ? -1 : 1)
      return productosSearch.map(productoSearch => productoSearch[0])
    }
    return productosContext
  }, [productosContext, search])
  const filters = useMemo(() => generateFilters(productosOriginales), [productosOriginales])
  const [activeFilters, setActiveFilters] = useState(Object.keys(productosContext[0].filters).map(key => [key.toUpperCase(), []]), [])
  const [displayedProducts, setDisplayedProducts] = useState(productosOriginales)

  const filterProducts = useCallback((activeFilters) => {
    const activeFiltersAmount = activeFilters.reduce((accumulator, currentValue) => accumulator + (currentValue[1].length === 0 ? 0 : 1), 0)
    if (activeFiltersAmount !== 0) {
      const finalFilteredProducts = [productosOriginales]
      activeFilters.forEach(filter => {
        if (filter[1].length > 0) {
          finalFilteredProducts.push([])
          filter[1].forEach(filterName => {
            finalFilteredProducts[finalFilteredProducts.length - 2].forEach(product => {
              if (filterName.toLowerCase() === product.filters[filter[0].toLowerCase()].toLowerCase()) {
                finalFilteredProducts[finalFilteredProducts.length - 1].push(product)
              }
            })
          })
        }
      })
      finalFilteredProducts[finalFilteredProducts.length - 1].sort((a, b) => a.id > b.id ? 1 : -1)
      return finalFilteredProducts[finalFilteredProducts.length - 1]
    }
    return productosOriginales
  }, [productosOriginales])

  //HANDLERS
  const filterButtonClickHandler = useCallback((isAdded, filterIndex, buttonName) => {
    const currentActiveFilters = activeFilters
    if (isAdded) {
      currentActiveFilters[filterIndex][1].push(buttonName)
    } else {
      const index = currentActiveFilters[filterIndex][1].indexOf(buttonName)
      if (currentActiveFilters[filterIndex][1].length > 0) {
        currentActiveFilters[filterIndex][1].splice(index, 1);
      } else {
        currentActiveFilters[filterIndex][1] = []
      }
    }
    const currentProducts = filterProducts(currentActiveFilters)
    setActiveFilters(currentActiveFilters)
    setDisplayedProducts(currentProducts)
  }, [activeFilters, filterProducts])

  useEffect(() => {
    if (selectedFilter) filterButtonClickHandler(true, 0, selectedFilter)
  }, [filterButtonClickHandler, selectedFilter])

  useEffect(() => {
    document.title = 'Productos | imondo ecommerce'
    showNavBar(true)
  }, [showNavBar])

  useEffect(() => {
    setDisplayedProducts(productosOriginales)
  }, [location, productosOriginales])

  return (
    <div className="ListaProductosView">
      {productosOriginales.length > 0 ? <FilterList filters={filters} search={search ? search : null} filterButtonClickHandler={filterButtonClickHandler} /> : null }
      <div className="ListaProductos-lista">
        {displayedProducts.length > 0 ? <ProductList products={displayedProducts} /> : <p className="ListaProductos-noproductos">No encontramos resultados que coincidan con este criterio</p>}
      </div>
      <p className="ListaProductos-redes-p">¡Si no pudiste encontrar un producto que buscabas, escribinos!</p>
      <Redes />
    </div>
  );
}

export default ListaProductosView