import React, { useState, useContext, useEffect } from 'react'
import './QuienRecibeView.css'
import { Link, Redirect } from 'react-router-dom'
import DatosCompraContext from '../../context/datosCompraContext'
import CarritoContext from '../../context/carritoContext'

function QuienRecibeView({ showNavBar }) {

  const { datosCompra, editarDatosCompra } = useContext(DatosCompraContext)
  const { productosCarrito } = useContext(CarritoContext)

  const [nombre, setNombre] = useState(datosCompra.nombre)
  const [DNI, setDNI] = useState(datosCompra.dni)
  const [direccion, setDireccion] = useState(datosCompra.direccion)
  const [numero, setNumero] = useState(datosCompra.numero)
  const [piso, setPiso] = useState(datosCompra.piso)
  const [dpto, setDepto] = useState(datosCompra.dpto)
  const [localidad, setLocalidad] = useState(datosCompra.localidad)
  const [provincia, setProvincia] = useState(datosCompra.provincia)
  const [codigoPostal, setCodigoPostal] = useState(datosCompra.codigoPostal)
  const [email, setEmail] = useState(datosCompra.email)
  const [aceptaNewsletter, setAceptaNewsletter] = useState(datosCompra.aceptaNewsletter)
  const [telefono, setTelefono] = useState(datosCompra.telefono)

  const continuarHandler = () => {
    const quienRecibe = {}
    quienRecibe.nombre = nombre
    quienRecibe.dni = DNI
    quienRecibe.direccion = direccion
    quienRecibe.numero = numero
    quienRecibe.piso = piso
    quienRecibe.dpto = dpto
    quienRecibe.localidad = localidad
    quienRecibe.provincia = provincia
    quienRecibe.codigoPostal = codigoPostal
    quienRecibe.email = email
    quienRecibe.aceptaNewsletter = aceptaNewsletter
    quienRecibe.telefono = telefono
    quienRecibe.metodo = ''
    editarDatosCompra(quienRecibe)
  }

  useEffect(() => {
    document.title = 'Quién recibe | imondo ecommerce'
    showNavBar(false)
  }, [showNavBar])


  return (

    <div className="QuienRecibeView">
      {productosCarrito.length !== 0 ?
        <div className="QuienRecibe-inner">
          <h2>QUIÉN RECIBE</h2>
          <form>
            <div className="QuienRecibe-input-container">
              <label>Nombre Completo</label>
              <input type="text" autoComplete="off" value={nombre} onChange={e => setNombre(e.target.value)} />
            </div>
            <div className="QuienRecibe-input-container">
              <label>DNI</label>
              <input type="text" autoComplete="off" value={DNI} onChange={e => setDNI(e.target.value)} />
            </div>
            <div className="QuienRecibe-input-container">
              <label>Dirección</label>
              <input type="text" autoComplete="off" value={direccion} onChange={e => setDireccion(e.target.value)} />
            </div>
            <div className="QuienRecibe-miniinput-container">
              <div className="QuienRecibe-miniinput">
                <label>N°</label>
                <input type="text" autoComplete="off" value={numero} onChange={e => setNumero(e.target.value)} />
              </div>
              <div className="QuienRecibe-miniinput">
                <label>Piso</label>
                <input type="text" autoComplete="off" value={piso} onChange={e => setPiso(e.target.value)} />
              </div>
              <div className="QuienRecibe-miniinput">
                <label>Dpto.</label>
                <input type="text" autoComplete="off" value={dpto} onChange={e => setDepto(e.target.value)} />
              </div>
            </div>
            <div className="QuienRecibe-mediuminput-container">
            <div className="QuienRecibe-codigopostal">
              <label>Código Postal</label>
              <input type="text" autoComplete="off" value={codigoPostal} onChange={e => setCodigoPostal(e.target.value)} />
            </div>
            <div className="QuienRecibe-provincia">
              <label>Provincia</label>
              <input type="text" autoComplete="off" value={provincia} onChange={e => setProvincia(e.target.value)} />
            </div>
            </div>
          <div className="QuienRecibe-input-container">
            <label>Localidad</label>
            <input type="text" autoComplete="off" value={localidad} onChange={e => setLocalidad(e.target.value)} />
          </div>
          <div className="QuienRecibe-input-container">
            <label>E-mail</label>
            <input type="text" autoComplete="off" value={email} onChange={e => setEmail(e.target.value)} />
          </div>
          <div className="QuienRecibe-input-container">
            <label>Teléfono</label>
            <input type="text" autoComplete="off" value={telefono} onChange={e => setTelefono(e.target.value)} />
          </div>
          <div className="QuienRecibe-input-container QuienRecibe-newsletter">
            <div style={{ backgroundColor: aceptaNewsletter ? 'black' : 'transparent', backgroundImage: 'url(' + process.env.PUBLIC_URL + '/imgs/quien-recibe/check.svg)' }} onClick={e => setAceptaNewsletter(prevAceptaNewsletter => !prevAceptaNewsletter)}></div>
            <label>Quiero suscribirme para recibir novedades y ofertas.</label>
          </div>
          <Link to='/como-pagas' className="QuienRecibe-link"><input className="QuienRecibe-submit" type="submit" value="CONTINUAR" onClick={continuarHandler} /></Link>
          </form>
        </div>
        : <Redirect to="/productos" />
}
    </div >
  );
}

export default QuienRecibeView;