import React, { useEffect } from 'react'
import './CompraErroneaView.css'
import { Link } from 'react-router-dom'

function CompraErroneaView({ showNavBar }) {

  useEffect(() => {
    showNavBar(false)
  }, [showNavBar])

  useEffect(() => {
    document.title = 'Compra erronea | imondo ecommerce'
  }, [])


  return (
    <div className="CompraErroneaView">
      <div className="CompraErronea-inner">
        <h2>Algo salió mal</h2>
        <div className="CompraErronea-mensajes">
          <p className="CompraErronea-mensaje">No pudimos procesar tu compra.</p>
          <p className="CompraErronea-mensaje">Por favor, volvé a intentarlo en un momento.</p>
        </div>
        <Link to="/" className="CompraErronea-volver">VOLVER AL SITIO</Link>
      </div>
    </div>
  );
}

export default CompraErroneaView;