import React, { useContext } from 'react'
import './Carrito.css'
import { Link } from 'react-router-dom'
import CarritoContext from '../../context/carritoContext'

function Carrito(props) {

  const { productosCarrito, agregarProductoAlCarrito, sacarProductoDelCarrito } = useContext(CarritoContext)

  return (
    <div className="Carrito" style={{ right: props.show ? '0px' : '-412px' }}>
      <div className="Carrito-titulo-contenedor">
        <h4 className="Carrito-titulo">Carrito ({productosCarrito.length})</h4>
        <button className="Carrito-cerrar" style={{ backgroundImage: 'url(' + process.env.PUBLIC_URL + '/imgs/carrito/cerrar.svg)' }} onClick={props.cerrarCarrito}></button>
      </div>
      {productosCarrito.map((producto, index) =>
        <div className="Carrito-producto" key={index} style={{ backgroundColor: index % 2 === 0 ? '#ececec' : 'transparent' }}>
          <div className="Carrito-producto-main">
            <div className="Carrito-producto-imagen" style={{ backgroundImage: 'url(' + producto.product.imageURLs[0] + ')' }}></div>
            <p>{producto.product.title}</p>
          </div>
          <p className="Carrito-producto-precio">{'$' + producto.product.price * producto.amount }</p>
          <div className="Carrito-producto-cantidad">
            <button style={{ backgroundImage: 'url(' + process.env.PUBLIC_URL +  '/imgs/carrito/restar.svg)' }} onClick={() => {agregarProductoAlCarrito(producto.product.id, -1)}}></button>
            <p>{producto.amount}</p>
            <button style={{ backgroundImage: 'url(' + process.env.PUBLIC_URL + '/imgs/carrito/sumar.svg)' }} onClick={() => {agregarProductoAlCarrito(producto.product.id, 1)}}></button>
          </div>
          <button className="Carrito-producto-eliminar" style={{ backgroundImage: 'url(' + process.env.PUBLIC_URL + '/imgs/carrito/tacho.svg)' }} onClick={() => sacarProductoDelCarrito(producto.product.id)}></button>
        </div>)}
        {productosCarrito.length > 0 ? <Link className="Carrito-checkout" to='/quien-recibe'><button>Terminar compra</button></Link> : null}
    </div>
  );
}

export default Carrito;