import React, { useContext, useEffect } from 'react'
import './CompraExitosaView.css'
import { Link } from 'react-router-dom'
import DatosCompraContext from '../../context/datosCompraContext'
import CarritoContext from '../../context/carritoContext'

function CompraExitosaView({ showNavBar }) {

  const { datosCompra } = useContext(DatosCompraContext)
  const { setProductosCarrito } = useContext(CarritoContext)

  useEffect(() => {
    showNavBar(false)
  }, [showNavBar])

  useEffect(() => {
    document.title = 'Compra exitosa | imondo ecommerce'
  }, [])

  useEffect(() => {
    setProductosCarrito([])
  }, [setProductosCarrito])


  return (
    <div className="CompraExitosaView">
      <div className="CompraExitosa-inner">
        <h2>¡COMPRA EXITOSA!</h2>
        <p className="CompraExitosa-mensaje">Te enviamos un e-mail con todos los detalles para mantenernos en contacto</p>
        {datosCompra.metodo === 'transferencia' ?
          <div>
            <div className="CompraExitosa-transferencia-datos">
              <p><b>TITULAR</b>: Juan Perez</p>
              <p><b>CBU:</b> 01234567890000067890</p>
              <p><b>ALIAS:</b> ARBOL.MANO.HOJA</p>
            </div>
            <p>Una vez acreditado el pago <u>contactate</u> con nosotros para confirmar el envío</p>
          </div>
          : null}
        <Link to="/" className="CompraExitosa-volver">VOLVER AL SITIO</Link>
      </div>

    </div>
  );
}

export default CompraExitosaView;