import React, { useState, useEffect } from 'react'
import './Carousel.css'

function Carousel(props) {

    const [selectedImgIndex, setSelectedImgIndex] = useState(props.selectedImgIndex ? props.selectedImgIndex : 0)

    const showPrevImg = () => {
        setSelectedImgIndex((prevImgIndex) => {
            return prevImgIndex === 0 ? props.imageURLs.length - 1 : prevImgIndex - 1
        })
    }

    const showNextImg = () => {
        setSelectedImgIndex((prevImgIndex) => {
            return prevImgIndex === props.imageURLs.length - 1 ? 0 : prevImgIndex + 1
        })
    }

    useEffect(() => {if (props.changeImage) props.changeImage(selectedImgIndex)}, [selectedImgIndex, props])

    return (
        <div className="carousel-container" style={{ ...props.style }}>

            <div id="carousel-content-container" style={{ marginLeft: selectedImgIndex * -100 + '%' }}>
                {props.imageURLs.map((imageURL, index) => <div className="carousel-element" key={index} style={{ backgroundImage: 'url(' + imageURL + ')' }}> </div>)}
            </div>

            {props.imageURLs.length > 1 && props.arrows ? <div id='carousel-arrows-container' onClick={e => e.stopPropagation()}>
                <button id="carousel-button-left" className="carousel-arrow-button" onClick={showPrevImg} ><img src={process.env.PUBLIC_URL + "/imgs/carousel/arrow-left.svg"} alt="anterior" /></button>
                <p>{selectedImgIndex + 1 + ' / ' + props.imageURLs.length}</p>
                <button id="carousel-button-right" className="carousel-arrow-button" onClick={showNextImg} ><img src={process.env.PUBLIC_URL + "/imgs/carousel/arrow-right.svg"} alt="siguiente" /></button>
            </div> : null}

        </div>
    )
}

export default Carousel