import React, { useContext, useState, useEffect } from 'react'
import './ProductoView.css'
import { useParams, Redirect } from 'react-router-dom'
import CarritoContext from '../../context/carritoContext'
import ProductosContext from '../../context/productosContext'
import ProductoPicDisplay from './ProductoPicDisplay'
import ProductoMobPicDisplay from './ProductoMobPicDisplay'
import ProductosRelacionados from './ProductosRelacionados'
import Redes from '../shared/Redes'

function ProductoView({ showNavBar }) {

  const [amount, setAmount] = useState(1)
  const [cantidadEnCarrito, setCantidadEnCarrito] = useState(0)
  const [displayThumbs, setDisplayThumbs] = useState(window.innerWidth > 900 ? true : false)

  const { productosCarrito, agregarProductoAlCarrito } = useContext(CarritoContext)
  const productoId = parseInt(useParams().id, 10)
  const producto = useContext(ProductosContext).find(producto => producto.id === productoId)
  const productosContext = useContext(ProductosContext)

  useEffect(() => {
    document.title = producto.title + ' | imondo ecommerce'
    showNavBar(true)
  }, [showNavBar, producto.title])

  useEffect(() => {
    setAmount(1)
    const productoEnCarrito = productosCarrito.find(producto => producto.product.id === productoId)
    setCantidadEnCarrito(productoEnCarrito ? productoEnCarrito.amount : 0)
  }, [productosCarrito, productoId])

  useEffect(() => {
    const setPicDisplay = () => setDisplayThumbs(() => window.innerWidth > 900 ? true : false)
    window.addEventListener("resize", setPicDisplay)
    return () => window.removeEventListener("resize", setPicDisplay)
  }, [])

  const handleUpButton = () => {
    if (amount + cantidadEnCarrito + 1 <= producto.stock) {
      setAmount(prevAmount => prevAmount + 1)
    }
  }

  return (
    <div className="ProductoView">
      {producto ? <div className="Producto-inner">
        <div className="ProductoView-main">
          {displayThumbs ? <div className="ProductoView-left">
            <ProductoPicDisplay imgs={producto.imageURLs} />
          </div> : null}
          <div className="ProductoView-right">
            <h3 className="ProductoView-title">{producto.title.toUpperCase()}</h3>
            <h4 className="ProductoView-price">{`$${producto.price}`}</h4>
            {displayThumbs ? null : <ProductoMobPicDisplay imageURLs={producto.imageURLs} />}
            {producto.description.text.map((text, index) => <p className="ProductoView-description-text" key={index}>{text}</p>)}
            {producto.description.items.map((item, index) => <p className="ProductoView-description-item" key={index}>{item}</p>)}
            {producto.stock - cantidadEnCarrito > 0 ?
              <div className="ProductoView-cart">
                <div className="ProductoView-cart-amount">
                  <p>{amount}</p>
                  <div className="ProductoView-cart-amount-buttons">
                    <button style={{ backgroundImage: 'url(' + process.env.PUBLIC_URL + '/imgs/producto/up.svg)' }} onClick={handleUpButton}></button>
                    <button style={{ backgroundImage: 'url(' + process.env.PUBLIC_URL + '/imgs/producto/up.svg)', transform: 'rotateX(180deg)' }} onClick={() => { if (amount - 1 > 0) setAmount(prevAmount => prevAmount - 1) }}></button>
                  </div>
                </div>
                <button className="ProductoView-cart-button" onClick={() => { agregarProductoAlCarrito(productoId, amount) }}>Agregar al carrito</button>
              </div>
              : producto.stock === 0 ?
                <p className="ProductoView-nostock">SIN STOCK</p>
                : <p className="ProductoView-nostock">Alcanzaste la cantidad máxima de unidades para este producto.<br />Terminá tu compra desde el carrito.</p>
            }
          </div>
        </div>
        <ProductosRelacionados productosRelacionados={producto.relatedProducts.map(relatedProduct => productosContext.find(product => product.id === relatedProduct))} />
        <p className="ProductosView-redes-p">¡Si te quedan dudas sobre este u otro producto, escribinos!</p>
        <Redes />
      </div>
        : <Redirect to='/' />
      }

    </div>
  );
}

export default ProductoView;