import React, { useContext, useEffect } from 'react'
import './ConfirmacionCompraView.css'
import { Link, Redirect } from 'react-router-dom'
import CarritoContext from '../../context/carritoContext'
import DatosCompraContext from '../../context/datosCompraContext'

function ConfirmacionCompraView({ showNavBar }) {

  const { productosCarrito } = useContext(CarritoContext)
  const { datosCompra } = useContext(DatosCompraContext)

  useEffect(() => {
    document.title = 'Confirmá tu compra | imondo ecommerce'
    showNavBar(false)
  }, [showNavBar])


  return (
    <div className="ConfirmacionCompraView">
      {productosCarrito.length === 0 ?
        <Redirect to="/productos" /> :
        datosCompra.nombre !== '' && datosCompra.telefono !== '' && datosCompra.metodo !== '' ?
          <div className="ConfirmacionCompra-inner">
            <h2>CONFIRMÁ TU COMPRA</h2>
            <div className="ConfirmacionCompra-productos">
              <div className="ConfirmacionCompra-productos-elements">
                {productosCarrito.map((producto, index) => <div className='ConfirmacionCompra-producto' key={index}><p className='ConfirmacionCompra-producto-nombre'>{producto.product.title + ' (x' + producto.amount + ')'}</p><p className='ConfirmacionCompra-producto-precio'>{'$' + producto.amount * producto.product.price}</p></div>)}
              </div>
              <div className="ConfirmacionCompra-productos-bottom">
                <Link to='/productos' className="ConfirmacionCompra-productos-agregar">+ Agregar Producto</Link>
                <p className="ConfirmacionCompra-productos-total">{'Total $' + productosCarrito.reduce(((total, producto) => total + producto.product.price * producto.amount), 0)}</p>
              </div>
            </div>
            <div className="ConfirmacionCompra-datos">
              <p><b>Nombre: </b>{datosCompra.nombre}</p>
              <p><b>DNI: </b>{datosCompra.dni}</p>
              <p><b>Teléfono: </b>{datosCompra.telefono}</p>
              <p><b>E-mail: </b>{datosCompra.email}</p>
              <p><b>Dirección: </b>{`${datosCompra.direccion} ${datosCompra.numero}, ${datosCompra.piso} ${datosCompra.dpto}. ${datosCompra.localidad}, ${datosCompra.provincia} (${datosCompra.codigoPostal})`}</p>
              <Link to='/quien-recibe' className="ConfirmacionCompra-datos-editar">Editar Datos</Link>
            </div>
            <div className="ConfirmacionCompra-metodoDePago">
              <p><b>Cómo pagás: </b>{datosCompra.metodo === 'transferencia' ? 'Transferencia Bancaria' : 'MercadoPago'}</p>
              <Link to='/como-pagas' className="ConfirmacionCompra-datos-editar">Editar Pago</Link>
            </div>
            <Link to="/compra-exitosa" className="ConfirmacionCompra-submit">CONFIRMAR COMPRA</Link>
          </div>
          : <Redirect to="/quien-recibe" />
      }
    </div>
  );
}

export default ConfirmacionCompraView;