import React, { useState, useContext } from 'react'
import './BurbujaCarrito.css'
import Carrito from './Carrito'
import CarritoContext from '../../context/carritoContext'

function BurbujaCarrito() {

    const [showCart, setShowCart] = useState(false)

    const { productosCarrito } = useContext(CarritoContext)

    const cartClickHandler = () => {
        setShowCart(prevShowCart => !prevShowCart)
    }

    return (
        <div className="BurbujaCarrito">
            <div className="BurbujaCarrito-button" style={{ backgroundImage: 'url(' + process.env.PUBLIC_URL + '/imgs/navbar/carrito.svg)' }} onClick={cartClickHandler}>
                {productosCarrito.length > 0 ? <p className="BurbujaCarrito-button-cantidad">{productosCarrito.length}</p> : null}
            </div>
            <Carrito show={showCart} cerrarCarrito={cartClickHandler} />
        </div>
    );
}

export default BurbujaCarrito;