import React, { useState, useEffect } from 'react'
import './NavBar.css'
import { Link, useLocation } from 'react-router-dom'
import Buscar from './Buscar'

function NavBar() {

  const location = useLocation()

  const [showBurguer, setShowBurguer] = useState(window.innerWidth > 900 ? false : true)
  const [showMenu, setShowMenu] = useState(false)

  useEffect(() => {
    const setMobile = () => {
      setShowBurguer(() => window.innerWidth > 900 ? false : true
      )
      if (window.innerWidth > 900) setShowMenu(false)
    }
    window.addEventListener("resize", setMobile)
    return () => window.removeEventListener("resize", setMobile)
  }, [])

  useEffect(() => setShowMenu(false), [location])

  return (
    <div className="NavBar" style={{ maxHeight: showMenu ? '250px' : '80px' }}>
      <div className="NavBar-content">
        <div className="NavBar-left">
          <Link to='/' className="NavBar-logo"><img className="NavBar-logo" src={process.env.PUBLIC_URL + "/imgs/logo.svg"} alt="logo" /></Link>
          {showBurguer ? <button className="NavBar-burguer-button" style={{ backgroundImage: 'url(' + process.env.PUBLIC_URL + '/imgs/navbar/burguer.svg)' }} onClick={() => setShowMenu(prevShowMenu => !prevShowMenu)}></button> : null}
        </div>
        <div className="NavBar-right">
          <div className="NavBar-buttons" style={{}}>
            <Buscar className="NavBar-buscar" />
            <Link to='/productos' className="NavBar-button">Productos</Link>
            <Link to='/como-comprar' className="NavBar-button">Cómo comprar</Link>
            <Link to='/contactanos' className="NavBar-button">Contactanos</Link>
          </div>
        </div>
      </div>
    </div>
  );
}

export default NavBar;