import React, { useEffect, useState } from 'react'
import './ContactoView.css'
import Redes from '../shared/Redes'

function ContactoView({ showNavBar }) {

  const [nombre, setNombre] = useState('')
  const [email, setEmail] = useState('')
  const [telefono, setTelefono] = useState('')
  const [mensaje, setMensaje] = useState('')

  useEffect(() => {
    document.title = 'Contacto | imondo ecommerce'
    showNavBar(true)
  }, [showNavBar])

  const submitHandler = () => {

  }

  return (
    <div className="ContactoView">
      <div className="Contacto-inner">
        <h2>CONTACTANOS</h2>
        <p>Completá el formulario y nos pondremos en contacto a la brevedad.</p>
        <form>
          <input type="text" autoComplete="off" placeholder="NOMBRE COMPLETO" value={nombre} onChange={e => setNombre(e.target.value)} />
          <input type="text" autoComplete="off" placeholder="E-MAIL" value={email} onChange={e => setEmail(e.target.value)} />
          <input type="text" autoComplete="off" placeholder="TELÉFONO" value={telefono} onChange={e => setTelefono(e.target.value)} />
          <textarea autoComplete="off" placeholder="MENSAJE" value={mensaje} onChange={e => setMensaje(e.target.value)} />
          <input className="Contacto-submit" type="submit" value="ENVIAR" onClick={submitHandler} />
        </form>
        <p className="Contacto-redes-p">¡También podés encontrarnos en nuestras redes sociales!</p>
        <Redes />
      </div>
    </div>
  );
}

export default ContactoView;