import React, { createContext, useState, useContext } from 'react'
import ProductosContext from './productosContext'

const CarritoContext = createContext()

const CarritoContextProvider = (props) => {

    const productosContext = useContext(ProductosContext)

    const [productosCarrito, setProductosCarrito] = useState(localStorage.getItem('carrito') ? JSON.parse(localStorage.getItem('carrito')) : [])

    const agregarProductoAlCarrito = (id, amount) => {
        if (productosCarrito.filter(producto => producto.product.id === id).length === 0) {
            setProductosCarrito(prevProductosCarrito => {
                const listaACambiar = []
                prevProductosCarrito.forEach(producto => listaACambiar.push(JSON.parse(JSON.stringify(producto))))
                listaACambiar.unshift({ product: productosContext.find(producto => producto.id === id), amount: amount })
                localStorage.setItem('carrito', JSON.stringify(listaACambiar))
                return listaACambiar
            })
        } else {
            setProductosCarrito(prevProductosCarrito => {
                const listaACambiar = []
                prevProductosCarrito.forEach(producto => listaACambiar.push(JSON.parse(JSON.stringify(producto))))
                const productoACambiar = listaACambiar.find(producto => producto.product.id === id)
                listaACambiar[listaACambiar.indexOf(productoACambiar)].amount += amount
                const result = listaACambiar[listaACambiar.indexOf(productoACambiar)].amount <= productosContext.find(producto => producto.id === id).stock && listaACambiar[listaACambiar.indexOf(productoACambiar)].amount > 0 ? listaACambiar : prevProductosCarrito
                localStorage.setItem('carrito', JSON.stringify(result))
                return result
            })
        }
    }

    const sacarProductoDelCarrito = (id) => {
        setProductosCarrito(prevProductosCarrito => {
            if (prevProductosCarrito.length > 1) {
                const result = prevProductosCarrito.filter(producto => producto.product.id !== id)
                localStorage.setItem('carrito', JSON.stringify(result))
                return result
            } else if (prevProductosCarrito[0].product.id === id) {
                localStorage.setItem('carrito', JSON.stringify([]))
                return []
            }
        })
    }

    return (
        <CarritoContext.Provider value={{ productosCarrito, setProductosCarrito, agregarProductoAlCarrito, sacarProductoDelCarrito }}>
            {props.children}
        </CarritoContext.Provider>
    )
}

export { CarritoContextProvider }
export default CarritoContext