import React, { useState } from 'react'
import { Switch, Route } from "react-router-dom"
import NavBar from './components/shared/NavBar'
import HomeView from './components/home/HomeView'
import ListaProductosView from './components/lista-productos/ListaProductosView'
import ProductoView from './components/producto/ProductoView'
import ComoComprarView from './components/como-comprar/ComoComprarView'
import ContactoView from './components/contacto/ContactoView'
import QuienRecibeView from './components/quien-recibe/QuienRecibeView'
import PagoView from './components/pago/PagoView'
import ConfirmacionCompraView from './components/confirmacion-compra/ConfirmacionCompraView'
import CompraExitosaView from './components/compra-exitosa/CompraExitosaView'
import CompraErroneaView from './components/compra-erronea/CompraErroneaView'
import BurbujaCarrito from './components/shared/BurbujaCarrito'
import { ProductosContextProvider } from './context/productosContext'
import { CarritoContextProvider } from './context/carritoContext'
import { DatosCompraContextProvider } from './context/datosCompraContext'

function App() {

  const [showNavBar, setShowNavBar] = useState(true)

  const handleViewChange = shown => {
    if (showNavBar !== shown) {
      setShowNavBar(shown)
    }
  }

  return (
    <ProductosContextProvider>
        <CarritoContextProvider>
          <DatosCompraContextProvider>
            <div className="App">
              {showNavBar ? <NavBar /> : null}
              <Switch>
                <Route exact path="/"><HomeView showNavBar={handleViewChange} /></Route>
                <Route exact path="/productos"><ListaProductosView showNavBar={handleViewChange} /></Route>
                <Route exact path="/productos/search=:search"><ListaProductosView showNavBar={handleViewChange} /></Route>
                <Route exact path="/producto/:id"><ProductoView showNavBar={handleViewChange} /></Route>
                <Route exact path="/como-comprar"><ComoComprarView showNavBar={handleViewChange} /></Route>
                <Route exact path="/contactanos"><ContactoView showNavBar={handleViewChange} /></Route>
                <Route exact path="/quien-recibe"><QuienRecibeView showNavBar={handleViewChange} /></Route>
                <Route exact path="/como-pagas"><PagoView showNavBar={handleViewChange} /></Route>
                <Route exact path="/confirma-compra"><ConfirmacionCompraView showNavBar={handleViewChange} /></Route>
                <Route exact path="/compra-exitosa"><CompraExitosaView showNavBar={handleViewChange} /></Route>
                <Route exact path="/compra-erronea"><CompraErroneaView showNavBar={handleViewChange} /></Route>
              </Switch>
              {showNavBar ? <BurbujaCarrito /> : null}
            </div>
          </DatosCompraContextProvider>
        </CarritoContextProvider>
    </ProductosContextProvider>
  );
}

export default App;