import React, { useEffect } from 'react'
import './ComoComprarView.css'
import Redes from '../shared/Redes'

function ComoComprarView({ showNavBar }) {

  useEffect(() => {
    document.title = 'Cómo comprar | imondo ecommerce'
    showNavBar(true)
  }, [showNavBar])

  return (
    <div className="ComoComprarView">
      <div className="ComoComprar-inner">
        <h2>CÓMO COMPRAR</h2>
        <div className="ComoComprar-steps">
          <div className="ComoComprar-step">
            <div>
              <h4>1</h4>
              <p>Elegí un producto de nuestro catálogo. Podés utilizar los filtros para encontrar fácilmente lo que buscás.</p>
            </div>
            <img src={process.env.PUBLIC_URL + "/imgs/como-comprar/flechas.svg"} alt="flecha" />
          </div>
          <div className="ComoComprar-step">
            <div>
              <h4>2</h4>
              <p>Seleccioná la cantidad de unidades y agregá el producto al carrito.</p>
            </div>
            <img src={process.env.PUBLIC_URL + "/imgs/como-comprar/flechas.svg"} alt="flecha" />
          </div>
          <div className="ComoComprar-step">
            <div>
              <h4>3</h4>
              <p>Abrí el carrito, asegurate de tener todo lo que quieras y apretá el botón <i>Terminar compra</i></p>
            </div>
            <img src={process.env.PUBLIC_URL + "/imgs/como-comprar/flechas.svg"} alt="flecha" />
          </div>
          <div className="ComoComprar-step">
            <div>
              <h4>4</h4>
              <p>Completá tus datos personales y de pago. Una vez confirmada la compra recibirás un e-mail con todos los detalles.</p>
            </div>
          </div>
        </div>
        <p className="ComoComprar-redes-p">¡Si te quedan dudas podés contactarnos a través de nuestras redes sociales!</p>
        <Redes />
      </div>
    </div>
  );
}

export default ComoComprarView;