import React, { useState, useEffect } from 'react'
import './ProductoPicDisplay.css'
import Carousel from '../shared/Carousel'

function ProductoPicDisplay(props) {

  const [bigDisplay, setBigDisplay] = useState(window.innerWidth > 850)
  const [selectedImg, setSelectedImg] = useState(0)
  const [showZoom, setShowZoom] = useState(false)

  const imgsThumbs = props.imgs.length > 4 ? props.imgs.slice(0, 4) : props.imgs

  const thumbClickHandler = index => {
    setSelectedImg(index)
  }

  useEffect(() => {

    //Fixing PicDisplay on window size change
    const changePicDisplay = () => {
      window.innerWidth > 850 ? setBigDisplay(true) : setBigDisplay(false)
    }

    //ComponentWillMount
    window.addEventListener('resize', changePicDisplay)

    //ComponentWillUnmount 
    return () => {
      window.removeEventListener('resize', changePicDisplay)
    }
  }, [])


  return (
    <div id="productoPicDisplay-container">
      <div id="productoPicDisplay-thumbs">
        {imgsThumbs.map((img, index) =>
          <div className="productoPicDisplay-thumb" key={index} style={{ backgroundImage: `url(${img})` }} onClick={() => thumbClickHandler(index)}></div>
        )}
        {props.imgs.length > 4 ? <div className="productoPicDisplay-thumb productoPicDisplay-thumb-plus" onClick={() => {thumbClickHandler(3); setShowZoom(true)}}><p>{`+${props.imgs.length - 3}`}</p></div> : null}
      </div>
      {bigDisplay ? <div id="productoPicDisplay-big-img" style={{ backgroundImage: `url(${props.imgs[selectedImg].replace('-mini', '')})` }} onClick={() => { setShowZoom(true) }} /> : null}
      {showZoom ? <div className="productoPicDisplay-modal" onClick={() => { setShowZoom(false) }}>
        <Carousel style={{ width: '80%', height: '80vh' }} selectedImgIndex={selectedImg} imageURLs={props.imgs} arrows={true} />
      </div> : null}
    </div>
  );
}

export default ProductoPicDisplay;