import React, { useState, useRef, useEffect } from 'react'
import './Buscar.css'
import { Redirect } from 'react-router-dom'

function Buscar() {

  const buscarInput = useRef(null)

  const [searchText, setSearchText] = useState('')
  const [showBar, setShowBar] = useState(false)
  const [redirect, setRedirect] = useState(false)

  useEffect(() => {
    if (showBar) buscarInput.current.focus()
  }, [showBar])

  const buttonClickHandler = () => {
    setShowBar(prevState => {
      if (searchText !== '' && prevState) {        
        setRedirect(true)
        setTimeout(() => setRedirect(false), 1000)
      }
      return !prevState
    })
  }

  return (
    <div className="Buscar" style={{ width: showBar ? '250px' : '20px', backgroundColor: showBar ? '#ececec' : 'transparent' }}>
      {showBar ? <input className="Buscar-input" type="text" maxLength="25" value={searchText} ref={buscarInput} onChange={e => setSearchText(e.target.value)} onKeyUp={e => {if (e.key === 'Enter' || e.keyCode === 13) buttonClickHandler()}} /> : null}
      <button className="Buscar-button" style={{ background: 'url(' + process.env.PUBLIC_URL + '/imgs/navbar/buscar.svg) no-repeat center', backgroundSize: 'contain' }} onClick={buttonClickHandler}></button>
      { redirect ? <Redirect to={'/productos/search=' + searchText} /> : null}
    </div>
  );
}

export default Buscar;