import React, { createContext, useState } from 'react'

const DatosCompraContext = createContext()

const DatosCompraContextProvider = (props) => {

    const [datosCompra, setDatosCompra] = useState(window.localStorage.getItem('datosCompra') ? JSON.parse(localStorage.getItem('datosCompra')) : { nombre: '', dni: '', direccion: '', numero: '', piso: '', dpto: '', localidad: '', provincia: '', codigoPostal: '', email: '', aceptaNewsletter: true, telefono: '', metodo: '' })

    const editarDatosCompra = (datosCompraInput) => {
            setDatosCompra(() => {
                localStorage.setItem('datosCompra', JSON.stringify(datosCompraInput))
                return datosCompraInput
            })
    }

    return (
        <DatosCompraContext.Provider value={{ datosCompra, editarDatosCompra }}>
            {props.children}
        </DatosCompraContext.Provider>
    )
}

export { DatosCompraContextProvider }
export default DatosCompraContext