import React, { useState, useEffect } from 'react'
import './FilterList.css'
import Filter from './Filter'
import { Link } from 'react-router-dom'

function FilterList(props) {

  const [smallScreen, setSmallScreen] = useState(window.innerWidth > 900 ? false : true)
  const [showFilters, setShowFilters] = useState(false)

  const filters = props.filters.map((filter, index) => {
    if (filter.buttons.length > 1) {
      return <Filter title={filter.title} buttons={filter.buttons} filterIndex={index} filterButtonClickHandler={props.filterButtonClickHandler} key={index} />
    }
    return null
  })

  useEffect(() => {
    const setMobile = () => {
      setShowFilters(false)
      setSmallScreen(() => window.innerWidth > 900 ? false : true
    )}
    window.addEventListener("resize", setMobile)
    return () => window.removeEventListener("resize", setMobile)
  }, [])

  return (
    <div id="filter-list-container" style={{ ...props.style, maxHeight: window.innerWidth <= 900 && showFilters ? '100vh' : '36px' }}>
      {smallScreen ? <button className="filter-list-mobile-button" onClick={() => setShowFilters(prevShowFilters => !prevShowFilters)}>FILTROS</button> : null}
      <div className="filter-list-inner" style={{ maxHeight: window.innerWidth <= 900 && showFilters ? '100vh' : window.innerWidth <= 900 ? '0' : '36px', paddingBottom: showFilters ? '15px' : '0' }}>
        {filters}
        {props.search ? <Link to="/productos" className="filter-list-search">{props.search.toUpperCase()}<img src={process.env.PUBLIC_URL + "/imgs/filtros/cerrar.svg"} alt="" /></Link> : null}
      </div>
    </div>
  )
}

export default FilterList