import React, { useState } from 'react'
import './ProductoMobPicDisplay.css'
import Carousel from '../shared/Carousel'

function ProductoMobPicDisplay(props) {

    const [showModal, setShowModal] = useState(false)
    const [selectedImage, setSelectedImage] = useState(0)

  return (
    <div id="productoMobPicDisplay">
        <div className="ProductoView-mobile-carousel" onClick={() => setShowModal(true)}><Carousel imageURLs={props.imageURLs} changeImage={index => setSelectedImage(index)} style={{ width: '100%', height: '100vw', maxHeight: '100vh', marginTop: '25px' }} arrows={true} />{showModal ? <div className="ProductoView-mobile-modal" style={{ backgroundImage: 'url(' + props.imageURLs[selectedImage] +')' }} onClick={e => { e.stopPropagation(); setShowModal(false) }}></div> : null}</div>
    </div>
  );
}

export default ProductoMobPicDisplay;