import React, { useContext, useEffect } from 'react'
import './PagoView.css'
import { Link, Redirect } from 'react-router-dom'
import DatosCompraContext from '../../context/datosCompraContext'
import CarritoContext from '../../context/carritoContext'

function PagoView({ showNavBar }) {

  const { datosCompra, editarDatosCompra } = useContext(DatosCompraContext)
  const { productosCarrito } = useContext(CarritoContext)

  const transferenciaHandler = () => {
    const quienRecibe = (JSON.parse(JSON.stringify(datosCompra)))
    quienRecibe.metodo = 'transferencia'
    editarDatosCompra(quienRecibe)
  }

  const mercadopagoHandler = () => {
    const quienRecibe = (JSON.parse(JSON.stringify(datosCompra)))
    quienRecibe.metodo = 'mercadopago'
    editarDatosCompra(quienRecibe)
  }

  useEffect(() => {
    document.title = 'Cómo pagás | imondo ecommerce'
    showNavBar(false)
  }, [showNavBar])


  return (
    <div className="PagoView">
      {productosCarrito.length === 0 ?
        <Redirect to="/productos" /> :
        datosCompra.nombre !== '' && datosCompra.telefono !== '' ?
          <div className="Pago-inner">
            <h2>CÓMO PAGÁS</h2>
            <div className="Pago-buttons">
              <Link to="/confirma-compra" className="Pago-button" onClick={transferenciaHandler}><span>TRANSFERENCIA</span><br />BANCARIA</Link>
              <Link to="/confirma-compra" className="Pago-button" onClick={mercadopagoHandler} style={{ backgroundImage: 'url(' + process.env.PUBLIC_URL + '/imgs/pago/mercadopago.png)' }}></Link>
            </div>
          </div>
          : <Redirect to="/quien-recibe" />
      }
    </div>
  );
}

export default PagoView;