import React from 'react'
import './Redes.css'

function Redes() {

    return (
        <div className="Redes">
            <button style={{ backgroundImage: 'url(' + process.env.PUBLIC_URL + '/imgs/contacto/wa.svg)' }} onClick={() => { window.open('https://api.whatsapp.com/send?phone=5491141675283&text=Quiero recibir información sobre los servicios de iMondo.', '_blank') }}></button>
            <button style={{ backgroundImage: 'url(' + process.env.PUBLIC_URL + '/imgs/contacto/ig.svg)' }} onClick={() => { window.open('https://instagram.com/imondodigital', '_blank') }}></button>
            <button style={{ backgroundImage: 'url(' + process.env.PUBLIC_URL + '/imgs/contacto/messenger.svg)' }} onClick={() => { window.open('https://m.me/imondodigital', '_blank') }}></button>
            <button className="Redes-fb" style={{ backgroundImage: 'url(' + process.env.PUBLIC_URL + '/imgs/contacto/fb.svg)' }} onClick={() => { window.open('https://facebook.com/imondodigital', '_blank') }}></button>
        </div>
    );
}

export default Redes;

