import React from 'react'
import './ProductosRelacionados.css'
import { Link } from 'react-router-dom'

function ProductosRelacionados({ productosRelacionados }) {

    return (
        <div className="ProductosRelacionados">
            <h3>Productos Relacionados</h3>
            <hr />
            <div className="ProductosRelacionados-container">
                {productosRelacionados.map((producto, index) => <Link to={'/producto/' + producto.id} className="ProductoRelacionado"  key={index}><div className="ProductoRelacionado-inner"><div style={{ backgroundImage: 'url(' + producto.imageURLs[0] + ')' }}></div><h4>{producto.title.toUpperCase()}</h4><p>{'$' + producto.price}</p></div></Link>)}
            </div>
        </div>
    );
}

export default ProductosRelacionados;