import React, { useState, useEffect } from 'react'
import ColorSelector from '../shared/ColorSelector'
import './ProductListElement.css'
import { Link } from 'react-router-dom'

function ProductListElement(props) {

    /* *** STATE *** */
    const [imgURL, setImgURL] = useState(props.product.colors ? props.product.colors[0].imageURLs[0] : props.product.imageURLs[0])


    useEffect(() => {
        if (props.product.colors) {
            setImgURL(props.product.colors[0].imageURLs[0])
        } else {
            setImgURL(props.product.imageURLs[0])
        }
    }, [props.product]) //Only adjust productListElement when props change

    /* *** FUNCTION CREATION *** */
    const changeColor = clickedColor => {
        props.product.colors.forEach(color => {
            if (color.name === clickedColor) {
                setImgURL(color.imageURLs[0])
            }
        })
    }

    const openProductCard = () => {

    }

    /* *** RENDERING *** */
    return (
        <div className="product-list-element" style={{ ...props.style }}>
            <Link to={'/producto/' + props.product.id} className="product-list-element-link"><div className="product-list-element-img" style={{ backgroundImage: 'url(' + imgURL + ')' }} onClick={openProductCard}></div></Link>
            <div className="product-list-element-text" >
                <Link to={'/producto/' + props.product.id} className="product-list-element-link"><p className="product-list-element-title" onClick={openProductCard}>{props.product.title.toUpperCase()}</p></Link>
                {props.product.stock === 0 ? <p className="product-list-element-nostock">SIN STOCK</p> : <p className="product-list-element-price">{'$' + props.product.price}</p>}
                {props.product.colors ? <ColorSelector colors={props.product.colors.map(color => color.name)} changeColor={changeColor} circleSize='15px' style={{ width: props.product.colors.length * 25 + "px", margin: 'auto' }} /> : props.product.logo ? <img src={process.env.PUBLIC_URL + '/imgs/' + props.product.logo.toLowerCase() + '.jpg'} alt={props.product.logo} style={{ height: "20px" }} /> : null}
            </div>
        </div>
    )

}

export default ProductListElement